"use client";

import { useState } from "react";
import OddsList from "./OddsList";
import SportTabs from "./SportTabs";

export default function OpportunitiesView({ initialResults }) {
  const [results] = useState(initialResults);
  const [sport, setSport] = useState("nba");

  return (
    <div className="space-y-6">

      <SportTabs active={sport} setActive={setSport} />

      <h2 className="text-xl font-bold neon">Odds Dashboard</h2>
      <p className="text-gray-400 text-sm">
        Switch sports to update the board in real-time.
      </p>

      <OddsList results={results} />
    </div>
  );
}
